<template>
	<div id="changePhone">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">修改手机号</div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff">
        <img src="static/img/icon_shouji--mf.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="number" placeholder="请输入旧手机号" v-model="oldphone"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img src="static/img/icon_shouji--mf.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="number" placeholder="请输入新手机号" v-model="newphone"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img src="static/img/icon_yanzmm.png" class="img_top_center width_5"/>
        <input class="font_14 login_input" type="password" placeholder="请输入验证码" v-model="code"/>
        <span class="font_12 float_right send_code" v-show="show" @click="testPhone">获取验证码</span>
        <span class="font_12 float_right send_code" v-show="!show">{{count}}s</span>
      </div>
      <div style="margin-top: 30px;" class="text_center">
        <button class="login_button" @click="checkRegister">确定修改</button>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'forgetPwd',
		data() {
			return {
        oldphone:'',
        phoneIn:'',
        code:'',
        codeIn:'',
        newphone:'',
        count: '',
        timer: null,
        show: true,
        userId:localStorage.getItem('userId')
			}
		},
		methods:{
      checkRegister(){
        if(this.phoneIn!=this.oldphone){
          this.$toast({
            message: '该手机号未发送验证码',
            position: "bottom"
          });
          return;
        }
        if(this.codeIn!=this.code){
          this.$toast({
            message: '验证码错误',
            position: "bottom"
          });
          return;
        }
        if(!this.newphone){
          this.$toast({
            message: '新手机号不可为空',
            position: "bottom"
          });
          return;
        }
        this.forgetPwd();
      },
      getCode(){
        let $this = this;
        let obj = {
          phone: $this.oldphone
        };
        let qs = require('qs');
        $this.$ajax.post("user/sendCode", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.getCodeIn();
            $this.codeIn = rpdata.data.data;
            $this.phoneIn = $this.oldphone;
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      getCodeIn(){
        const TIME_COUNT = 60;
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000)
        }
      },
      testPhone(){
        if(!this.oldphone||!this.valiPhone(this.oldphone)){
          this.$toast({
            message: '请输入正确的手机号',
            position: "bottom"
          });
        }else{
          this.getCode();
        }
      },
      forgetPwd(){
        let $this = this;
        let obj = {
          userid: this.userId,
          oldphone: $this.oldphone,
          newphone: $this.newphone
        };
        let qs = require('qs');
        $this.$ajax.post("user/changephone", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '修改手机号成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      }
		}
	}
</script>
<style scoped>
  .login_button {
    width: 80%;
    height: 40px;
    line-height: 40px;
    border: 0;
    color: #ffffff;
    border-radius: 5px;
    background-color: #c10804;
  }
  .send_code{
    padding: 2px 5px;
    border: 1px solid #c10804;
    border-radius: 15px;
    color: #c10804;
  }
  .login_span{
    position: relative;
    left: 43%;
    bottom: 32px;
    color: #ffffff;
  }
  .login_btn{
    width: 96%;
    margin-left: 2%;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_box{
    margin-top: 44px;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
</style>



